








import Vue from 'vue';
import { loginGoogle } from '../../api/login';

export default Vue.extend({
	methods: {
		async handleLogin() {
			try {
				const GoogleUser = await this.$gAuth.signIn();
				const userData = {
					email: GoogleUser.dt.Nt,
					avatar: GoogleUser.dt.DJ,
					username: GoogleUser.dt.Ue,
					token: GoogleUser.mc.access_token,
				};
				const { data } = await loginGoogle(userData);
				userData.token = data.token;
				this.$store.commit('setUserData', userData);
				this.$store.commit(
					'openSnackbar',
					`로그인 성공! 안녕하세요 ${userData.username}님!`,
				);
				this.$router.replace('/');
			} catch (error) {
				throw new Error(error);
			}
		},
	},
});
