<template>
	<v-btn x-large block color="primary" @click="loginAnonymous"
		>익명으로 로그인</v-btn
	>
</template>

<script>
import { loginAnonymous } from '../../api/login';

export default {
	methods: {
		async loginAnonymous() {
			try {
				const { data } = await loginAnonymous();
				const userInfo = {
					username: '익명',
					avatar: '',
					email: '',
					token: `${data.insertId}`,
				};
				this.$store.commit('setUserData', userInfo);
				this.$store.commit(
					'openSnackbar',
					`로그인 성공! 안녕하세요 ${userInfo.username}님!`,
				);
				this.$router.replace('/');
			} catch (error) {
				throw new Error(error);
			}
		},
	},
};
</script>

