import { $http } from './index';
import { UserInfo } from '../types/index';
import { AxiosPromise } from 'axios';

export const loginAnonymous = (): AxiosPromise => {
	return $http.post('/login/anony');
};

export const loginGoogle = (userData: UserInfo): AxiosPromise => {
	return $http.post('/login/google', userData);
};
